
body
{
  background-color: #222831;
}.app{
  margin-top: 25px;
  padding-top: 10px;
    text-align: center;
    height: 90vh;
    width: 300px;
    background-color: #31363F;
    color:bisque;
    margin-left: 400px;
    border-radius: 25px;
  }
  .add
  {
    padding: 3px 7px;
    background-color: #76ABAE ;
    color:white;
    font-size: 16px;
    border-radius: 4px;
    border: 0;
    margin-left: 7px;
    cursor: pointer;
  }
  .delete-button{
    font-size: 14px;
    margin-left: 38px;
    margin-top: 5px;
    margin-bottom: 5px;
    background-color: rgb(147, 43, 43);
    border: none;
    color: white;
    border-radius: 8px;
    padding-top: 4px;
    padding-bottom: 4px;
    cursor: pointer;
  }
  ul li{list-style-type: none;}
  
  .update-button{
    background-color: #222831;
    color: #EEEEEE;
    margin-left: 3px;
    border: 0;
    padding: 5px;
    border-radius: 5px 10px;
    cursor: pointer;
    font-size: 14px;
  }
  input{
    background-color: transparent;
    border: 3px solid #222831;
    font-size: 16px;
    color: bisque;
    padding: 4px 0px;
  }